<template>
  <div id="message">
    <div class="list">
      <div class="item flex-row pT20" @click="getCode"><button>获取code</button></div>
      <div class="item" v-for="(item, index) in list" :key="index">
        <input type="text" v-model="item.code" placeholder="请复制你的code">
        {{$utils.timestampToStr(item.timestamp, 'Y/M/D h:m:s')}}
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      code: '',
      list: [
        {
          code: 12,
          timestamp: new Date().getTime()
        }
      ]
    }
  },
  computed: {
    commonState () {
      return this.$store.state.common
    }
  },
  methods: {
    getCode () {
      let w = window.plus.nativeUI.showWaiting('获取code中')
      setTimeout(() => {
        w.close()
      }, 2000)
      this.commonoauthInfo['weixin'].authorize(e => {
        w.close()
        e.timestamp = this.$utils.getTimeStamp() * 1000
        this.list = [e, ...this.list]
      },
      () => {
        w.close()
      })
    },
    getWechatInfo () {
      
    },
    getOauth () {
      this.$utils.plusReady(plus => {
        let oauthInfo = {}
        plus.oauth.getServices(services => {
          for (let v of services) {
            oauthInfo[v.id] = v
          }
          this.$store.commit('common_oauthInfo', oauthInfo)
        })
      })
    }
  }
}
</script>
<style lang="scss" scoped>
.item {
  border-top: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
  margin-top: -1px;
  height: 80px;
  line-height: 80px;
  overflow: hidden;
  display: flex;
  input {
    display: block;
    padding: 0 30px;
    height: 100%;
    border-right: 1px solid #ddd;
    box-sizing: border-box;
    flex: 1;
    min-width: 0;
    margin-right: 10px;
  }
  font-size: 28px;
  color: #999;
}
</style>



